import React from 'react';
//Img:
import platziLogo from '../assets/img/platzi.png';
import codeaLogo from '../assets/img/codearock.png';
import codecampLogo from '../assets/img/freecodecamp.png';
import telefonicaLogo from '../assets/img/telefonica.jpg';
import computerLogo from '../assets/img/Pink-desk-with-computer.jpg'
//Slider:
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/pagination';
import '../assets/css/Cursos.css';


const Cursos =()=> {
  const cursitos = [
  { img: platziLogo,
    title: 'Platzi',
    name: 'Curso de Programacin Bsica',
    description:
             'Programa para estudiantes que se inician en Javascript,HTML y  algoritmos.',
  },
  { img: codeaLogo,
    title: 'CodeaRock',
    name:'HTML for Babies & Javascript desde Cero',
    description: 'Cursos asincrnicos en los que se cubren los contenidos bsicos para empezar a programar pginas web estticas e interactivas.', 
  },  
  { img: codeaLogo,
    title: 'CodeaRock',
    name:'Winterhack',
    description: 'Curso sincrnico intensivo en el que se trabajan nociones de: HTML, JS, React, Node.Js, Firebase y GitHub.', 
  }, 
  { img: codecampLogo,
    title: 'FreeCodeCamp',
    name:'React desde Cero',
    description: 'Curso asincrnico terico-prctico en el que se cubren los conceptos bsicos de React as como su implementacin en diversos proyectos.', 
  },  
  { img: codecampLogo,
    title: 'FreeCodeCamp',
    name:'Javascript para Manipulacin del DOM',
    description: 'Curso prctico para manipular el DOM de una pgina web mediante el uso de clases y selectores, el manejo de eventos y la creacin y eliminacin dinmica de elementos.'
  },
  { img: telefonicaLogo,
    title: 'Fundacin Telefnica - Conecta Empleo',
    name: 'Gestin de Proyectos con Metodologas giles y Enfoques Lean',
    description:'Curso terico sobre metodologas giles en la implementacin de proyectos colaborativos; Manifiesto gil; artefactos y roles en Scrum;  mtodo Kanban.'
  },
  {img: computerLogo,
    title: 'Comenzando un nuevo curso',
    name: 'en breve.',
    description: 'Con muchas ganas de aprender :)'
  }
  ]
  return (
    <div className='cursos-wrapper' id='Cursos'>
      <div className='cursos-titulo'>
        <h2 className='cursos-h2'>Mis Cursos</h2>
      </div> 
      <div className='cursos-background'>
        <div className='cursos-p'>
          <p className='cursos-p'>stos son algunos de los cursos online que he hecho hasta el momento:</p>    
        </div>
          <Swiper
             spaceBetween={20}
             slidesPerView={2}
             grabCursor={true}  
             className='curso-slider'           
            >
           
            {cursitos.map((cursito, index)=>{
              return(
                <SwiperSlide key={index} className='curso-card'>
                  <div>
                    <img src={cursito.img} className='curso-img' alt='Logo'/>
                  </div>
                  <div className='description-container'>
                    <span className='title'>{cursito.title}</span>
                    <span className='name'>{cursito.name}</span>
                    <span className='description'>{cursito.description}</span>
                  </div>
                </SwiperSlide>
              )
            })}
         </Swiper>
       </div> 
    </div>
  )
}
export default Cursos;